* {
  box-sizing: border-box;
}

html, body, #root, .App {
  width: 100%;
  height: 100%;
} 

#root {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: rgb(242, 242, 242);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  max-width: 600px;
  background-color: white;
}

.App main {
  position: relative;
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.App main #done-message {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#done-message img {
  width: 50%;
}

.App main .column {
  overflow-y: scroll;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  padding: 100px 30px 10px 10px;
  display: flex;
  flex-direction: column-reverse;
}

.App main .entry {
  flex: 0;
  display: flex;
  flex-direction: column; 
  align-items: flex-end;
  padding: 0 10px 10px 10px;
  width: 100%;
  border-top: 1px solid #ccc;
}

#create-button {
  position: absolute;
  bottom: 10px;
  right: 10px;
  --radius: 30px;
  width: calc(var(--radius) * 2);
  height: calc(var(--radius) * 2);
  line-height: calc(2*var(--radius) - 5px);
  border-radius: var(--radius);
  border: 0;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.244);
  background-color: rgb(56, 175, 255);
  color: white;
  font-size: 2.5em;
  text-align: center;
}
#create-button:hover {
  cursor: pointer;
}



.App > nav {
  flex-grow: 0;
}

.App > nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-top: 1px solid #ccc;
  width: 100%;
}

.App nav > button {
  background-color: transparent;
  border: none;
  font-size: 1.5em;
  border-top: 2px solid transparent;
  text-align: center;
}

.App nav > button.selected {
  border-top-color: rgba(0, 0, 255, 0.423);
}

.App nav > button:hover {
  cursor: pointer;
  /* background-color: rgb(237, 237, 237); */
}

.text {
  font-size: 1rem;
}

.entry .close-button {
  border: none;
  background-color: transparent;
  width: min-content;
  font-size: 1.5em;
}

.entry > form {
  display: flex;
  flex-direction: row;
  align-items: flex-end; 
  border: 1px solid #ccc;
  width: 100%;
}

.entry > form > textarea {
  flex: 1;
  resize: none;
  width: 100%;
  min-height: 100px;
  border: none;
}

.entry > form > textarea:focus {
  outline: none !important;
}

.entry > form > input {
  height: min-content;
  font-size: 1em;
  float: right;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: #fafafa;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  padding: 5px 20px 10px 20px;
  margin: 10px;
  width: 100%;
}

.card header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  color: rgb(158, 158, 158);
}

.card .details > * {
  margin: 2px;
  padding: 0;
  font-size: 0.75em;
}

/* Break very long words */
.card .text {
  word-break: break-word;
  white-space: pre-wrap;
}

.inbox .quick-actions {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* add space between */
}

.inbox .quick-actions button {
  background-color: Transparent;
  border: none;
}

.inbox .quick-actions button img {
  filter: grayscale(100%) opacity(50%);
}

.inbox .quick-actions button:hover {
  background-color: rgb(237, 237, 237);
  cursor: pointer;
}

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  flex-shrink: 1;
}

.info > * {
  text-align: left;
  margin-bottom: 5px;
  width: 100%;
  overflow-wrap: break-word;
}

.actions {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-grow: 0;
  flex-shrink: 0;
  margin-left: 20px;
}

.actions > * {
  background-color: transparent;
  border: none;
  margin-bottom: 5px;
}
.actions > *:hover {
  cursor: pointer;
}

.card .quick-actions {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.card .quick-actions > button {
  font-size: 1em;
}


